/* Normal App */
.container {
	padding-top: 15px;
}

.sidebar-container {
	height:100%;
}

.bottom-row {
	position:absolute;
	bottom: 0;
}

#spacer {
	height: 150px;
}

.language-card {
	margin-bottom: .5rem;
}

.graph-column {
	position: fixed;
	left: 0;
	top: 0;
}

.sidebar-column {
	position: absolute;
	right: 0;
	top: 0;
}

.quote {
	border-left: 2px solid rgba(0, 0, 0, 0.5);
	padding-left: 5px;
}

/* No Mobile */
@media (max-width: 768px) {
	html, body, #root {
		width: 100%;
		height: 100%;
	}
}

.no-mobile {
	text-align: center;
}